@font-face {
    font-family: 'Open Sans';
    src: url('../fonts/open-sans/opensans-regular-webfont.woff2') format('woff2'),
         url('../fonts/open-sans/opensans-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Open Sans Semibold';
    src: url('../fonts/open-sans/opensans-semibold-webfont.woff2') format('woff2'),
         url('../fonts/open-sans/opensans-semibold-webfont.woff') format('woff'),
         url('../fonts/open-sans/opensans-semibold-webfont.eot');
    font-weight: 600;
    font-style: normal;
}
@font-face {
    font-family: 'Open Sans Italic';
    src: url('../fonts/open-sans/opensans-italic-webfont.woff2') format('woff2'),
         url('../fonts/open-sans/opensans-italic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: italic;
}
@font-face {
    font-family: 'Open Sans Light';
    src: url('../fonts/open-sans/opensans-light-webfont.woff2') format('woff2'),
         url('../fonts/open-sans/opensans-light-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
body{
    padding: 0;
    margin: 0;
    font-family: "Open Sans";
    font-size: 16px;
}

.header{
    width: 100%;
    height: 100px;
    background-color: #ffffff;
    position: fixed;
    box-shadow: 0px 10px 20px -7px rgba(155, 159, 163, 0.4);
    z-index: 1;
}

.header_content{
    box-sizing: border-box;
    padding: 0 15px;
    margin: 0 auto;
    max-width: 1220px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo_img{
    height: 80px;
    width: 80px;
}

.header_ul{
    list-style: none;
    display: flex;
    align-items: center;
    padding-left: 0;
    .header_link{
        text-decoration: none;
        color: #6a6a66;
        font-size: 14px;
    }
    li{
        padding: 10px 15px;
        &:last-child{
        margin-right: 0;
        padding: 10px 15px;
        border-radius: 25px;
        background-color: #337ab7;
            a{
            color:#fff;
            }
        }   

        &:hover{
            a{
                color:#3d80ba;
            }
            border-radius: 5px;
            background-color: #eee;
            &:last-child{
                border-radius: 25px;
            background-color: #eee;
            }
        }
    }   
}

.form{
    width:100%;
    background-color: #f7f7f7;
    .content_form{
        box-sizing: border-box;
        padding: 0 15px;
        margin: 0 auto;
        max-width: 1220px;
        display: flex;
        flex-direction: row;
        padding-top: 110px;
        .courses{
            max-width: 534px;
            padding: 110px 33px 0px 33px;
            .courses_title{
                font-family:'Open Sans Semibold';
                max-width: 538px;
                text-align:center;
                font-size: 33px;
                color:#333333;
                letter-spacing: normal;
                padding-bottom: 20px;
            }
            .courses_text{
                max-width: 500px;
                margin:0 auto;
                font-family: 'Open Sans Light';
                font-size: 18px;
                color: #737878;
                letter-spacing: normal;
                text-align: center;
                font-weight: normal;
                line-height: 1.7;
            }
        }
        .registration{
            max-width: 590px;
            padding-top: 40px;
            letter-spacing: 0.6px;
            padding-bottom:20px ;
            #reg_href{
                margin-top: -200px;
                position: absolute;
            }
            .registration_title{
                font-family: "Open Sans Semibold";
                font-size: 20px;
            }
            .ur{
                color: #70686d;
                padding-top: 11px;
                padding-bottom: 9px;
                font-family: 'Open Sans Semibold';
                font-size: 14px;
                letter-spacing: normal;
                &:first-child{
                    padding-top: 22px;
                }
            }
            .required_img{
                width: 7px;
                height: 7px;
                padding-bottom: 2px;
            }
            .inputs{
                height:50px;
                max-width: 568px;
                width: 100%;
                border-radius: 5px;
                border: 1px solid #856a7c;
                padding: 0 10px;
            }
            .input_padding{
                padding-right: 20px;
            }
            .inputs:focus{
                box-shadow:  0 0 13px rgba(102, 175, 233, 1);
                border: 1px solid #66afe9;
                outline: none;
            }
            input:active{
                color: #66afe9;
            }
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            }
            .checkbox{
                margin-top: 18px;
                display: flex;
                flex-direction:row;
                font-family:"Open Sans Light";
                font-size:14px;
                color:#464545;
                .required_img_two{
                    width: 7px;
                    height: 7px;
                    margin-bottom: 4px;
                }
                #check{
                    width: 14px;
                    height: 14px;
                    align-self: center;
                    margin: 0 7px 0 0;
                }
                a{
                    text-decoration: none;
                }
            }
            .button_registration{
                max-width: 568px;
                display:flex;
                padding-top: 25px;
                justify-content: center;
                .button_text{
                    background-color: #337ab7;
                    padding:13px 67px;
                    border-radius: 25px;
                    border: 0;
                    text-align: center;
                    font-size: 14px;
                    color: #fff;
                    text-transform: uppercase;
                    &:hover{
                        color: #337ab7 ;
                        border-radius: 25px;
                        background-color: #cecece;
                    }
                }
            }
        }
    }
}
.develop{
    background-color: #fff;
    #dev_href{
        margin-top: -100px;
        position: absolute;
    }
    .develop_content{
        box-sizing: border-box;
        padding: 0 15px;
        margin: 0 auto;
        max-width: 1220px;
        text-align: center;
        padding-top: 40px;
        padding-bottom: 40px;
        .develop_title{
            font-family:'Open Sans';
            font-size: 33px;
            color: #333;
            max-width: 400px;
            height: 40px;
            padding: 20px 0;
        }
        .first_slider{
            min-width: 0;
            .slick-list{
                overflow: hidden;
            }
            .slick-track{
                display: flex;
                margin: 0 auto;
            }
            .slick-dots{
                display: flex;
                justify-content: center;
                align-content: center;
                padding: 0;
                li{
                    list-style: none;
                    button{
                        font-size: 0;
                        width: 15px;
                        height: 15px;
                        background-color:#b3b3b3 ;
                        border: none;
                        border-radius: 50%;
                        margin: 0 7px;
                    }
                }
                li.slick-active{
                    list-style: none;
                    button{
                        font-size: 0;
                        width: 15px;
                        height: 15px;
                        background-color:#666 ;
                        border: none;
                        border-radius: 50%;
                        margin: 0 7px;
                    }
                }
            }
            .slide{
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 40px 23px 20px;
                min-width: 0;
                .slide_title{
                    font-family: "Open Sans";
                    font-size: 23px;
                    color: #666;
                    padding: 20px 0;
                    border-bottom: 1px solid #3498db;
                }
                .slide_text{
                    font-family: 'Open Sans Light';
                    color: #979797;
                    text-align: center;
                    margin-top: 20px;
                    font-size: 16px;
                    letter-spacing: 0.4px;
                }
            }
        }
    }
}
.stat{
    background-color: #3498db;
