            }
            .stat_text{
                font-family: 'Open Sans Light';
                font-size: 14px;
                letter-spacing: normal;
                color: #fff;
                text-align: center;
            }
            #stat_img_second{
                margin-bottom: 10px;
                margin-top: -7px;
            }
            .stat_img_second{
                height: 60px;
                width: 70px;
            }
            #stat_img_third{
                margin-bottom: 10px;
                margin-top: -7px;
            }
            .stat_img_third{
                height: 60px;
                width: 70px;
            }
            #stat_img_fourth{
                margin-bottom: 10px;
                margin-top: -7px;
            }
            .stat_img_fourth{
                height: 60px;
                width: 70px;
            }
        }   
    }
}
.review{
    background-color: #ffffff;
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
    z-index: 0;
