        box-sizing: border-box;
        padding: 0 15px;
        margin: 0 auto;
        max-width: 1220px;
        padding: 60px 0 10px;
        .review_title{
            font-family: 'Open Sans';
            font-size: 30px;
            color: #333333;
            letter-spacing: normal;
            text-align: center;
            padding: 0 40px;
        }
        .second_slider{
            min-width: 0;
            .slick-dots{
                display: flex;
                justify-content: center;
                align-content: center;
                padding: 60px 0 0;
                li{
                    list-style: none;
                    button{
                        font-size: 0;
                        width: 15px;
                        height: 15px;
                        background-color:#b3b3b3 ;
                        border: none;
                        border-radius: 50%;
                        margin: 0 7px;
                    }
                }
                li.slick-active{
                    list-style: none;
                    button{
                        font-size: 0;
                        width: 15px;
                        height: 15px;
                        background-color:#666 ;
                        border: none;
                        border-radius: 50%;
                        margin: 0 7px;
                    }
                }
            }
            .slick-list{
                overflow: hidden;
            }
            .slick-track{
                display: flex;
            }
            .review_slide{
                display: flex;
                flex-direction: column;
                max-width: 880px;
                min-width: 0;
                margin: 0 auto;
                padding: 0 10px;
                        .fa-quote-left{
                            text-align: left;
                            color:#c4c4c4;
                            font-size: 40px;
                        }
                        .fa-quote-right{
                            text-align: right;
                            color: #c4c4c4;
                            font-size: 40px;
                        }
                        .review_text{
                            max-width: 800px;
                            font-family: "Open Sans Light";
                            font-size: 18px;
                            color: #767676;
                            text-align: center;
                            letter-spacing: 0.5px;
                            line-height: 1.75;
                            padding: 0 40px;
                        }
                 
                .human{
                    .name{
                        text-align: center;
                        font-family: "Open Sans";
                    }
                    .post{
                        font-family: "Open Sans Italic";
                        color:#969696;
                        text-align: center;
                    }
                }
            }
        }

    }
}
.video{
    #video_href{
        margin-top: -100px;
        position: absolute;
    }
    background-color: #f7f7f7;
    .video_content{
        max-width: 1020px;
        margin: 0 auto;
        padding: 50px 0;
        .video_title{
            padding-bottom: 40px;
            font-family: "Open Sans";
            font-size: 30px;
            margin: 0 auto;
            display: flex;
            color:#333;
            justify-content: center;
        }
