            }
        }
    }
}
@media(max-width: 767px){
    .header{
        height: 100px;
        background-color: #fff;
        width: 100%;
        z-index: 10;
    }
    .header_content{
        z-index: 10;
    }
    .logo{
        z-index: 10;
    }
    .burger_background{
        width: 50px;
        height: 40px;
        background-color: #337ab7;
        border-radius: 5px;
        display: flex;
        z-index: 10;
        justify-content: center;
        .header_burger{
            display: block;
            position: relative;
            width: 30px;
            height: 20px;
            top: 10px;
            z-index: 10;
        & span{
            content: '';
            background-color: #fff;
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            top: 9px;
        }
        &:before,
        &:after{
            content: '';
            background-color: #fff;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
        }
        &:before{
            top:0;
        }
        &:after{
            bottom: 0;
        }
        }
    }
    .burger_menu{
        position: fixed;
        height: 280px;
        background-color: #fff;
        top: -100%;
        width: 100%;
        display: flex;
        left: 0;
        justify-content: center;
        transition: all 0.3s ease 0s;
        z-index: 1;
        box-shadow: 0px 10px 20px -7px rgba(155, 159, 163, 0.4);
        &.active{
            top:95px;
        }
    }
    .header_ul{
        display: block;
        text-align: center;
        margin: 0;
        z-index: 1;
        .header_link{
        font-size: 18px;
    }
    }
    .stat{
        background-color: #3498db;
        width: 100%;
        padding: 0;
        .stat_content{
            box-sizing: border-box;
            padding: 0 15px;
            margin: 0 auto;
            max-width: 1220px;
            display: grid;
            min-height: 400px;
            grid-template-columns: 1fr 1fr;
        }
    }
    .contacts{
        .contacts_content{
            box-sizing: border-box;
            padding: 0 15px;
            margin: 0 auto;
            max-width: 1220px;
            display: grid;
            min-height: 400px;
            grid-template-columns: 1fr 1fr;
        }
    }
}
